import { ChangeDetectionStrategy, Component, Input, TemplateRef } from '@angular/core';
import { NgIfContext } from '@angular/common';

@Component({
  selector: 'bb-loans-main-container',
  templateUrl: './loans-main-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoansMainContainerComponent {
  /**
   * Flag represents error state
   *
   * @default: false
   */
  @Input() isErrorState: boolean | null = false;
  /**
   * Flag represents loading  state
   *
   * @default: false
   */
  @Input() isLoadingState: boolean | null = false;
  /**
   * Flag represents empty state
   *
   * @default: false
   */
  @Input() isEmptyState: boolean | null = false;
  /**
   * Flag will add the additional class for state containers to have some min height
   * Applicable for empty error and loading default states only
   *
   * @default: false
   */
  @Input() containerHeightFitToContent = false;
  /**
   * Classes for inner content element wrapper
   *
   * @default: card-body
   */
  @Input() contentWrapperClasses: string[] | string = 'bb-card__body';
  /**
   * Classes for root wrapper element
   */
  @Input() rootWrapperClasses: string[] | string = 'bb-card';
  /**
   * Custom error state template
   */
  @Input() customErrorState!: TemplateRef<NgIfContext<undefined>>;
  /**
   * Custom loading state template
   */
  @Input() customLoadingState!: TemplateRef<NgIfContext<undefined>>;
  /**
   * Custom empty state template
   */
  @Input() customEmptyState!: TemplateRef<NgIfContext<undefined>>;
}
